<template>
    <el-form ref="form" :model="form" label-width="95px" class="content-form">
        <el-form-item label="">
            <el-input v-model="form.title"></el-input>
        </el-form-item>
        <el-form-item label=" ">
            <el-checkbox v-model="form.is_secrecy"></el-checkbox>
        </el-form-item>
        <el-form-item label="">
            <el-select v-model="form.rooms" placeholder="" filterable>
                <el-option :label="item.title" :value="item.room_id" :key="item.room_id" v-for="item in meetingRoom"></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="">
            <el-date-picker v-model="form.start_time" type="datetime" placeholder="" :editable="false" value-format="yyyy-MM-dd HH:mm:ss"></el-date-picker>
        </el-form-item>
        <el-form-item label="">
            <el-date-picker v-model="form.end_time" type="datetime" placeholder="" :editable="false" value-format="yyyy-MM-dd HH:mm:ss"></el-date-picker>
        </el-form-item>
        <el-form-item label="">
            <el-input v-model="form.conferee_num"></el-input>
        </el-form-item>
        <el-form-item label="">
            <el-input v-model="form.clerk_id"></el-input>
        </el-form-item>
        <el-form-item label="">
            <el-input v-model="form.department"></el-input>
        </el-form-item>
        <el-form-item label="">
            <el-checkbox-group v-model="form.services">
                <el-checkbox :label="item.config_id" :key="item.config_id" v-for="item in roomsConfigList">{{ item.title }}</el-checkbox>
            </el-checkbox-group>
        </el-form-item>
        <el-form-item label="">
            <el-checkbox-group v-model="form.functions">
                <el-checkbox :label="1"></el-checkbox>
                <el-checkbox :label="2"></el-checkbox>
                <el-checkbox :label="3"></el-checkbox>
                <el-checkbox :label="4"></el-checkbox>
                <el-checkbox :label="5"></el-checkbox>
                <el-checkbox :label="6"></el-checkbox>
                <el-checkbox :label="7"></el-checkbox>
                <el-checkbox :label="8"></el-checkbox>
                <el-checkbox :label="9"></el-checkbox>
                <el-checkbox :label="10"></el-checkbox>
            </el-checkbox-group>
        </el-form-item>
        <el-form-item label="">
            <el-input v-model="form.description"></el-input>
        </el-form-item>
        <el-form-item label=" ">
            <el-button type="primary" size="small" @click="submit('form')"></el-button>
        </el-form-item>
    </el-form>
</template>
<script>
//  => 
export default {
    data() {
        return {
            checked: false,
            form: {
                title: '',
                is_secrecy: false,
                rooms: '',
                start_time: '',
                end_time: '',
                conferee_num: '',
                clerk_id: '',
                department: '',
                services: [],
                functions: [],
                description: ''
            },
            meetingRoom: [],
            roomsConfigList: []
        }
    },
    created() {
        this.getMeetingRoom()
        this.getRoomsConfigList()
    },
    methods: {
        getMeetingRoom() {
            this.$api.apiCommunication('/Rooms/getRoomsList', {}, response => {
                if (response.status === 200) {
                    if(response.data !== []) {
                        this.meetingRoom = response.data.list
                    } else {
                        this.meetingRoom = []
                    }
                } else {
                    this.$alert(`${response.data}`, '', { confirmButtonText: '', type: 'error' })
                }
            })
        },
        getRoomsConfigList() {
            this.$api.apiCommunication('/Rooms/getRoomsConfigList', {}, response => {
                if (response.status === 200) {
                    if(response.data !== []) {
                        this.roomsConfigList = response.data.list
                    } else {
                        this.roomsConfigList = []
                    }
                } else {
                    this.$alert(`${response.data}`, '', { confirmButtonText: '', type: 'error' })
                }
            })
        },
        submit(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    let param = {}
                    Object.assign(param, this.form)
                    param.services = param.services.join(',')
                    param.functions = param.functions.join(',')
                    param.is_secrecy = param.is_secrecy ? 1 : 0
                    console.log(param)
                    this.$api.apiCommunication('/Meeting/createMeeting', param, response => {
                        if (response.status === 200) {
                            this.$notify({ title: '', message: '', type: 'success' })
                            // this.$refs[formName].resetFields()
                        } else {
                            this.$alert(`${response.data}`, '', { confirmButtonText: '', type: 'error' })
                        }
                    })
                } else {
                    this.$notify({ title: '', message: '', type: 'error' })
                }
            })
        },
    }
}
</script>



